.message{
    margin: 0px 10px 10px 20px;
    display: flex;
    align-items: center;
}
.messageBlock{
    background: #fff;
    border-radius: 20px 20px 20px 0px;
    padding: 10px;
    max-width: 80%;
    overflow-x: hidden;
}
.myMessage{
    justify-content: flex-end;
}
.myMessage .messageBlock{
    background: #212121;
    color: #ffffff;
    border-radius: 20px 20px 0px 20px;
}
